.myСases{position:relative;margin-top:150px;background-color:#ebf3ff;color:#ebf3ff;background:linear-gradient(90deg, rgb(0, 30, 97) 0%, rgb(56, 117, 221) 100%);border:1px solid rgba(255,0,0,0)}.myСases .arrow{position:relative;z-index:3}.myСases .arrow button{background-color:rgba(56,116,221,0);border:0px}.myСases .arrow button:hover{background-color:#e2e2e2}.myСases .arrow img{margin:10px;width:100px}.myСases .titleContainer{display:flex;align-items:center;margin:20px 0}.myСases .titleContainer p{margin:0 20px}.myСases .info{z-index:2}.myСases .button{position:relative;z-index:2;border:2px solid #fff;background-color:rgba(235,243,255,0);border-radius:7px;color:#fff;width:315px;height:70px;font-size:18px;-webkit-box-shadow:0px 0px 19px 0px rgba(255,255,255,.2);-moz-box-shadow:0px 0px 19px 0px rgba(255,255,255,.2);box-shadow:0px 0px 19px 0px rgba(255,255,255,.2)}.myСases .container{position:relative}.myСases .background{position:absolute;z-index:1}@media screen and (min-width: 1920px){.myСases{font-size:20px}.myСases .title{font-size:30px}.myСases .info{width:900px}.myСases .background{height:900px;top:-200px;right:0px}.myСases .button{margin:65px 0}.myСases .subTitle{margin:70px 0 20px 0;font-size:24px}}@media screen and (max-width: 1919px){.myСases{font-size:20px}.myСases .title{font-size:30px}.myСases .info{width:600px}.myСases .background{height:800px;top:-150px;right:-50px}.myСases .button{margin:65px 0}.myСases .subTitle{margin:70px 0 20px 0;font-size:24px}}@media screen and (max-width: 1023px){.myСases{font-size:16px}.myСases .info{width:470px}.myСases .background{height:570px;top:-110px;right:-50px}.myСases .arrow img{width:76px}.myСases .button{margin:10px 0 45px 0}.myСases .subTitle{margin:70px 0 20px 0;font-size:19px}.myСases .title{font-size:24px}}